@-webkit-keyframes skew {
  0% {
    transform: skewX(6deg) translate(-6px);
  }

  50% {
    transform: skewX(-6deg) translate(6px);
  }

  100% {
    transform: skewX(6deg) translate(-6px);
  }
}

@keyframes skew {
  0% {
    transform: skewX(6deg) translate(-6px);
  }

  50% {
    transform: skewX(-6deg) translate(6px);
  }

  100% {
    transform: skewX(6deg) translate(-6px);
  }
}

@-webkit-keyframes sway {
  0% {
    transform: rotate(4deg);
  }

  50% {
    transform: rotate(-4deg);
  }

  100% {
    transform: rotate(4deg);
  }
}

@keyframes sway {
  0% {
    transform: rotate(4deg);
  }

  50% {
    transform: rotate(-4deg);
  }

  100% {
    transform: rotate(4deg);
  }
}

@-webkit-keyframes swaybot {
  0% {
    transform: rotate(-4deg);
  }

  50% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(-4deg);
  }
}

@keyframes swaybot {
  0% {
    transform: rotate(-4deg);
  }

  50% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(-4deg);
  }
}

.flash {
  height: 100%;
  width: 100%;
  position: fixed;
  display: flex;
  overflow: hidden;
  background-color: black;
  -webkit-animation: lightningflash 5s linear infinite;
          animation: lightningflash 5s linear infinite;
}
/*animation: flash 7s linear infinite;*/

.lightning {
  position: absolute;
  height: 160px;
  width: 150px;
  top: -10px;
  background-position-y: center;
  background-position-x: center;
  background-size: cover;
  -webkit-animation: lightningshape 7.5s steps(1, end) infinite;
          animation: lightningshape 7.5s steps(1, end) infinite;
}

@-webkit-keyframes lightningflash {
  0%,
  30%,
  36%,
  63%,
  69%,
  100% {
    opacity: 0;
    background-color: #646464;
  }
  33%,
  66% {
    opacity: 1;
  }
}

@keyframes lightningflash {
  0%,
  30%,
  36%,
  63%,
  69%,
  100% {
    opacity: 0;
    background-color: #646464;
  }
